* {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
}

button{
    outline: none;
}

.button:hover{
    background-color: #ee8d8d;
    cursor: pointer;
    transform: scale(1.03);
}

body{
    background : #f5f5f5;
    margin:0;
    overflow: scroll;
}

h3{
    font-size: 1.5em;
    color: #7b7b7b;
}

hr{
    height:1px;
    border-width:0;
    background-color: #e7e7e7
}

.objWorkList p{
    width: 100px;
    text-align: center;
}

::-webkit-scrollbar {
    width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #fff;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #ECECEC;
    border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #DDDDDD ;
}

.app {
    margin: 0;
    padding: 0;
}

/*__________Side bar menu__________*/

.sideBar{
    height: 100%;
    float: left;
    position: absolute;
    background-color: #6E0000;
    border-radius: 0 15px 15px 0;
    transition: 250ms;
    display: flex;
    flex-flow: column;
}

.sbOpened{
    width: 230px;
}

.sbClosed{
    width: 100px;
}

.sideBar header{
    text-align: right;
    margin: 10px;
    font-weight: bolder;
    font-size: 20px;
    color: #FFD4D4;
    transition: 200ms;
}

.sideBar span:hover {
    cursor: pointer;
    color: #FFFFFF;
    transform: scale(1.01);
}

.sideBar ul{
    list-style: none;
    padding: 0;
    margin: 0 25px;
}

.listContainer img{
    width: 25px;
}

.sideBar li{
    color: #FFD4D4;
    text-align: center;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    transition: 250ms;
    border-radius: 10px;
    padding: 5px;
    margin: 20px 0;
}

.sideBar li:hover{
    cursor: pointer;
    transform: scale(1.01);
    color: #ffa6a6;
    box-shadow: 0 2px 5px #b43a3a;
}

.listContainer{
    width: 100%;
    flex-grow : 1;
    background-color: #851313;
    border-radius: 0 50px 15px 0;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.listContainer span {
    color: #A7A7A7;
    font-size: small;
}

.noUnder{
    text-decoration: none;
}

.userProfile{
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 10px 0;
}

.profileImgNormal{
    width: 100px;
    transition: 250ms;
    border-radius: 50%;
    box-shadow: 0 2px 5px #ef6d6d;
}
.profileImgSmall{
    width: 60px;
    transition: 250ms;
    border-radius: 50%;
    box-shadow: 0 2px 5px #ef6d6d;
}

.userProfile p{
    margin: 10px 0;
    color: #FFD2D2;
    transition: 250ms;
    font-size: 0.8em;
}

.infoBox{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    background-color: #6E0000;
    border-radius: 15px;
    padding: 10px;
    margin: 50px 20px;
    position:relative;
    top: 35px;
}
.infoBox img {
    height: 50px;
    width: 50px;
}

.dataCont{
    display: flex;
    flex-direction: column;
    font-size: 0.6em;
    align-items: flex-start;
    justify-content: space-evenly;
}

.dataCont p{
    margin: 8px 0;
    color: white;
    font-size: 8px;
}

/*__________Main__________*/

.main{
    background-color: white;
    height: 97vh;
    transition: 250ms;
}

