/* _______ Inicio Login _______*/


@import url('https://fonts.googleapis.com/css?family=Montserrat:400,800');

.mainContainerLogin * {
	box-sizing: border-box;
}

.mainContainerLogin {
	background: #f6f5f7;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	font-family: 'Montserrat', sans-serif;
	height: 100vh;
	margin: -20px 0 50px;
}

.mainContainerLogin h1 {
	font-weight: bold;
	margin: 0;
}

.mainContainerLogin h2 {
	text-align: center;
}

.mainContainerLogin p {
	font-size: 14px;
	font-weight: 100;
	line-height: 20px;
	letter-spacing: 0.5px;
	margin: 20px 0 30px;
}

.mainContainerLogin span {
	font-size: 12px;
}

.mainContainerLogin a {
	color: #333;
	font-size: 14px;
	text-decoration: none;
	margin: 15px 0;
}

.mainContainerLogin button {
	border-radius: 15px;
	border: none;
    margin-top: 15px;
	background-color: #851313;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 35px;
	letter-spacing: 1px;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
}

.mainContainerLogin button:active {
	transform: scale(0.95);
}

.mainContainerLogin form {
	background-color: #FFFFFF;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 50px;
	height: 100%;
	text-align: center;
}

.mainContainerLogin input {
	border: 2px solid #aaa;
    border-radius: 4px;
    box-sizing: border-box;
	padding: 8px;
	margin: 8px 0;
    outline: none;
    transition: .3s;
	width: 100%;
}

.mainContainerLogin input:focus {
    border-color: dodgerblue;
    box-shadow: 0 0 8px 0;
}

.idIcon input {
    padding-left: 40px;
}

.idIcon {
    position: relative;
}

.overlay-container .idIcon img {
    position: absolute;
    color: #aaa;
    left: 0;
    padding: 9px 8px;
    transition: .3s;
    top: 8px;
    width: 15%;
}

.mainContainerLogin .container {
	background-color: #fff;
	border-radius: 10px;
  	box-shadow: 0 14px 28px rgba(0,0,0,0.25), 
			0 10px 10px rgba(0,0,0,0.22);
	position: relative;
	overflow: hidden;
	width: 768px;
	max-width: 100%;
